dth: 100%;\n  background: rgba(0, 0, 0, 0.5);\n  top: 0;\n  left: 0;\n}\n\n.active .stall-head::after,\n.active .stall-body::after {\n  display: none;\n}\n\n.stall-support {\n  height: 100px;\n  width: 90%;\n  box-sizing: border-box;\n  border-left: 10px solid brown;\n  border-right: 10px solid brown;\n  position: absolute;\n  top: 70px;\n  left: 5%;\n}\n\n.stall-body {\n  height: 130px;\n  width: 100%;\n  border: 2px solid black;\n  position: absolute;\n  bottom: 0px;\n  background-color: white;\n}\n.image-container {\n  width: 100%;\n  height: 100%;\n  overflow: hidden;\n}\n.logo-container {\n  width: 100%;\n  height: 100%;\n  overflow: hidden;\n}\n\n.logo-container img {\n  object-fit: contain;\n  width: 100%;\n  height: 100%;\n}\n.image-container img {\n  object-fit: cover;\n  width: 100%;\n  height: 100%;\n}\n","$walking-man-width: 30400px;\n$walking-man-height: 600px;\n\n$walking-woman-width: 9600px;\n$walking-woman-height: 360px;\n\n.sprite {\n  position: absolute;\n  z-index: 10;\n  bottom: 30px;\n}\n\n.walking-man-wrapper {\n  position: absolute;\n  bottom: 0;\n  transform: translate(-65%, 0);\n}\n\n.walking-man {\n  overflow: hidden;\n}\n\n.walking-man.man {\n  width: 165px;\n  height: 195px;\n}\n\n.walking-man.woman {\n  width: 165px;\n  height: 195px;\n}\n\n.walking-man.man .image-wrapper {\n  width: $walking-man-width;\n  height: $walking-man-height;\n  background-image: url(\"./assets/walking-man.png\");\n  transform: scale(0.3);\n}\n\n.walking-man.woman .image-wrapper {\n  width: $walking-woman-width;\n  height: $walking-woman-height;\n  background-image: url(\"./assets/walking-woman.png\");\n  transform: scale(0.55);\n}\n\n.walking-man .image-wrapper {\n  background-repeat: no-repeat;\n  transform-origin: top left;\n}\n\n.walking-man.man .image-wrapper.walking {\n  background-image: url(\"./assets/walking-man.png\");\n  animation: walk 0.8s steps(38) infinite;\n}\n\n.walking-man.woman .image-wrapper.walking {\n  background-image: url(\"./assets/walking-woman.png\");\n  animation: walkWoman 0.8s steps(20) infinite;\n}\n\n.walking-man.man .walking-reverse {\n  background-image: url(\"./assets/walking-man-reverse.png\") !important;\n  animation: walk 0.8s steps(38) infinite reverse;\n}\n\n.walking-man.woman .walking-reverse {\n  background-image: url(\"./assets/walking-woman-reverse.png\") !important;\n  animation: walkWoman 0.8s steps(20) infinite reverse;\n}\n\n.walking-man.man .reverse-man {\n  background-image: url(\"./assets/walking-man-reverse.png\") !important;\n}\n\n.walking-man.woman .reverse-man {\n  background-image: url(\"./assets/walking-woman-reverse.png\") !important;\n}\n\n@keyframes walk {\n  0% {\n    background-position: 0 0;\n  }\n  100% {\n    background-position: calc($walking-man-width * -1) 0;\n  }\n}\n\n@keyframes walkWoman {\n  0% {\n    background-position: 0 0;\n  }\n  100% {\n    background-position: calc($walking-woman-width * -1) 0;\n  }\n}\n",".character-buttons-wrapper {\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  margin-top: -100px;\n  margin-left: -100px;\n  color: white;\n  display: flex;\n  gap: 50px;\n  z-index: 11;\n\n  button {\n    border: none;\n    width: 100px;\n    height: 100px;\n    aspect-ratio: 1;\n    transition: transform 0.1s ease-in;\n\n    img {\n      width: 100%;\n      height: 100%;\n    }\n  }\n\n  button:hover {\n    transform: scale(1.1);\n  }\n\n  button:active {\n    transform: scale(1);\n  }\n}\n","$main: #e04e4c;\n$second: #493e44;\n\n.gate-container {\n  /*position: absolute;\n  left: 20%;\n  bottom: -30px;*/\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  flex-direction: column;\n  gap: 20px;\n  height: 100%;\n\n  .welcome-message {\n    color: white;\n    font-size: 24px;\n  }\n\n  .gate {\n    position: relative;\n    width: 250px;\n    height: 140px;\n    left: 16px;\n\n    .upper,\n    .pillar,\n    .upper-child {\n      position: absolute;\n    }\n\n    .upper {\n      z-index: 20;\n      width: 90%;\n      height: 20px;\n      background: $second;\n      border-radius: 0 0 16px 16px;\n    }\n\n    .upper-child {\n      border-radius: 0 0 16px 16px;\n      z-index: 15;\n      background: $main;\n      width: 70%;\n      height: 20px;\n      top: 16px;\n      left: 10%;\n      box-shadow: inset 0 -0.5vmin 0 rgba(#000, 0.1);\n    }\n\n    .pillar {\n      z-index: 3;\n      width: 24px;\n      height: 240px;\n      background: $main;\n      left: 15%;\n      box-shadow: inset -0.5vmin 0 0 rgba(#000, 0.1);\n      &:before {\n        position: absolute;\n        content: \"\";\n        width: 32px;\n        height: 26px;\n        background: $second;\n        bottom: 0;\n        left: -4px;\n        border-radius: 4px;\n      }\n    }\n\n    .left {\n      z-index: 11;\n      left: 65%;\n    }\n  }\n}\n","$pole-dark: #1e2635;\n$pole-light: #233451;\n\n.streetlamp {\n  $streetlamp-height: 380px;\n  width: 80px;\n  height: $streetlamp-height;\n  position: relative;\n  top: calc(524px - $streetlamp-height);\n  max-width: 80px;\n  min-width: 80px;\n\n  .base {\n    bottom: 0;\n    right: 0;\n    position: absolute;\n    width: 81%;\n    height: 24%;\n    background: linear-gradient(\n        -94deg,\n        $pole-dark 0 23%,\n        #fff0 calc(23% + 1px) 100%\n      ),\n      linear-gradient(-1deg, ($pole-dark) 0 3.65%, #fff0 calc(1.65% + 1px) 100%),\n      linear-gradient(\n        85deg,\n        ($pole-dark) 0 58%,\n        ($pole-light) calc(58% + 1px) 100%\n      );\n    border-radius: 2px;\n    clip-path: polygon(15% 0, 76% 0, 95% 99%, 0% 99%);\n    z-index: 1;\n  }\n\n  .basetop {\n    background: #1e2634;\n    width: 75%;\n    height: 3%;\n    position: absolute;\n    bottom: 24%;\n    left: 16%;\n    border-radius: 2px;\n    z-index: 2;\n    transform: rotate(-0.5deg);\n  }\n\n  .pole {\n    position: absolute;\n    width: 25%;\n    height: 56.5%;\n    background: linear-gradient(\n        -1deg,\n        ($pole-dark) 0 1.65%,\n        #fff0 calc(1.65% + 1px) 100%\n      ),\n      linear-gradient(-91deg, ($pole-dark) 0 35%, #fff0 calc(35% + 1px) 100%),\n      linear-gradient(\n        89.5deg,\n        ($pole-dark) 0 40%,\n        ($pole-light) calc(40% + 1px) 100%\n      );\n    bottom: 26.25%;\n    left: 45%;\n    clip-path: polygon(17% 0, 60% 0, 95% 99%, 0% 99%);\n    z-index: 1;\n  }\n\n  .poletop {\n    width: 20%;\n    height: 1.5%;\n    background: ($pole-dark);\n    position: absolute;\n    bottom: 82%;\n    left: 44%;\n    z-index: 1;\n    clip-path: polygon(0% 0, 100% 0, 95% 65%, 10% 72%);\n  }\n\n  .head {\n    position: absolute;\n    width: 100%;\n    height: 100px;\n    bottom: 83%;\n    z-index: 1;\n\n    .glass {\n      background: linear-gradient(\n          -79deg,\n          #fff0 0 12%,\n          ($pole-dark) 13% 16%,\n          #fff0 17% 100%\n        ),\n        conic-gradient(\n          from -46deg at 53% 200%,\n          #fff0 0 13%,\n          ($pole-dark) 13.2% 14%,\n          #fff0 14.2% 100%\n        ),\n        linear-gradient(\n          70deg,\n          #fff0 0 21%,\n          ($pole-dark) 22% 25%,\n          #fff0 26% 100%\n        ),\n        radial-gradient(circle at 37% 250%, #fff0 0 73%, #e4ce8b 75% 100%),\n        radial-gradient(circle at 37% 300%, #fff0 0 73%, #e4ce8b 75% 100%),\n        conic-gradient(\n          from -46deg at 53% 200%,\n          #fff0 0 7%,\n          #f5f3b0 0 13%,\n          ($pole-dark) 13.2% 14%,\n          #fff0 14.2% 100%\n        ),\n        linear-gradient(\n          -79deg,\n          #fff0 0 12%,\n          ($pole-dark) 13% 16%,\n          #ffe5ad 17% 50%,\n          #fff0 17% 100%\n        );\n      background-repeat: no-repeat;\n      background-size:\n        100% 100%,\n        100% 100%,\n        100% 100%,\n        33% 42%,\n        50% 35%,\n        100% 100%;\n      background-position:\n        0 0,\n        0 0,\n        0 0,\n        90% 0,\n        20% 0,\n        0 0;\n      width: 100%;\n      height: 45%;\n      top: 50%;\n      position: absolute;\n      transform: rotate(-3deg);\n      opacity: 0.5;\n    }\n  }\n\n  .head .top {\n    background: conic-gradient(\n      from 131deg at 55% 0,\n      ($pole-light) 0 12%,\n      ($pole-dark) 13% 29.5%,\n      #fff0 0 100%\n    );\n    width: 100%;\n    height: 30%;\n    top: 20%;\n    position: absolute;\n    transform: rotate(-3deg);\n  }\n\n  .top:before {\n    content: \"\";\n    position: absolute;\n    background: ($pole-light);\n    width: 8px;\n    height: 12px;\n    border-radius: 100%;\n    left: 48%;\n    top: -40%;\n    border: 3px solid ($pole-dark);\n    border-left-width: 0;\n  }\n\n  .top:after {\n    content: \"\";\n    position: absolute;\n    background: ($pole-dark);\n    width: 4px;\n    height: 12px;\n    border-radius: 100%;\n    left: 52%;\n    top: -70%;\n  }\n\n  .head .bot {\n    width: 75%;\n    height: 5%;\n    background: linear-gradient(\n        45deg,\n        #fff0 12%,\n        ($pole-dark) 0 65%,\n        #fff0 0 100%\n      ),\n      linear-gradient(-50deg, #fff0 8%, ($pole-dark) 0 65%, #fff0 0 100%);\n    left: 17%;\n    position: absolute;\n    bottom: 1%;\n    transform: rotate(-3deg);\n  }\n\n  .light {\n    background: linear-gradient(180deg, #dac47080, #dac47020 90%, #fff0 100%);\n    width: 650px;\n    height: calc($streetlamp-height - 50px);\n    position: absolute;\n    left: -274px;\n    bottom: 1.5%;\n    clip-path: polygon(45% 1%, 53% 0.35%, 80% 100%, 20% 100%);\n    opacity: 0;\n  }\n  .category {\n    background: transparent;\n    border: none;\n  }\n  .light.visible {\n    opacity: 1;\n  }\n\n  .head .glass {\n    animation: turn-on 2s ease 0s 1;\n    opacity: 1;\n  }\n\n  @keyframes turn-on {\n    0%,\n    10%,\n    25% {\n      opacity: 0;\n    }\n    5%,\n    15%,\n    35%,\n    100% {\n      opacity: 1;\n    }\n  }\n\n  .categories {\n    width: 90px;\n    height: 90px;\n    border-radius: 50%;\n    color: white;\n    background-color: rgba(245, 243, 176, 0.8);\n    position: absolute;\n    z-index: 10;\n    opacity: 0;\n    top: 0;\n    color: black;\n    left: 0;\n    transition: all 1.2s ease-in;\n  }\n\n  #button1.visible {\n    top: 170px;\n    left: -120px;\n    opacity: 1;\n  }\n\n  #button2.visible {\n    left: -170px;\n    top: 70px;\n    opacity: 1;\n  }\n\n  #button3.visible {\n    top: -30px;\n    left: -150px;\n    opacity: 1;\n  }\n\n  #button4.visible {\n    top: -128px;\n    left: -90px;\n    opacity: 1;\n  }\n\n  #button5.visible {\n    top: -140px;\n    left: 30px;\n    opacity: 1;\n  }\n\n  #button6.visible {\n    top: -110px;\n    left: 140px;\n    opacity: 1;\n  }\n\n  #button7.visible {\n    top: -10px;\n    left: 180px;\n    opacity: 1;\n  }\n\n  #button8.visible {\n    top: 90px;\n    left: 170px;\n    opacity: 1;\n  }\n\n  #button9.visible {\n    left: 120px;\n    top: 170px;\n    opacity: 1;\n  }\n}\n"],"sourceRoot":""}]);
